import {useState} from "react"


export default function Phone(params) {
  return (
    <svg
      className="phone"
      style={params.style}
      width="194" height="416" viewBox="0 0 194 416" fill="none"
      xmlns="http://www.w3.org/2000/svg"
      onClick={params.closeMonitor}
    >
      <foreignObject style={{cursor:"pointer"}} onClick={params.onClick} x="3" y="3" width="188" height="410" rx="8">
        <video ref={params.appVideo} xmlns="http://www.w3.org/1999/xhtml" width="188" height="410" controls="" loop play>
          <source src={require("../assets/app_recording.mp4")} type="video/mp4" />
        </video>
      </foreignObject>
      <rect x="3" y="3" width="188" height="410" rx="8" stroke="black" strokeWidth="6"/>
      <circle cx="96.5" cy="13.5" r="4.5" fill="black"/>
    </svg>
  )
}
