import './styles/App.css';
import "./styles/page1.css"
import "./styles/page2.css"
import "./styles/page3.css"
import { useState, useEffect, useRef } from "react"

import Sky from './assets/page_1/sky.svg';
import Ground from "./assets/page_1/ground.svg"
import Mountain1 from "./assets/page_1/mountain1.svg"
import Mountain2 from "./assets/page_1/mountain2.svg"
import Mountain3 from "./assets/page_1/mountain3.svg"
import Moon from "./assets/page_1/moon.svg"
import Hut from "./components/hut"

import Room1 from "./components/Room1"
import Room1Mobile from "./components/Room1Mobile"
import Phone from "./components/Phone"
import Monitor from "./components/Monitor"

import Cave1 from "./assets/page_3/1.svg"
import Cave2 from "./assets/page_3/2.svg"
import Cave3 from "./assets/page_3/3.svg"
import Cave4 from "./assets/page_3/4.svg"
import Cave5 from "./assets/page_3/5.svg"

function App() {
  const WindowWidthLessThan = (size) => {
    const [width, setWidth] = useState(0)
    useEffect(() => {
      const handleResize = () => setWidth(window.innerWidth)
      window.addEventListener("resize", handleResize)
      handleResize()
      return () => { 
        window.removeEventListener("resize", handleResize)
      }
    }, [setWidth])
    return width <= size
  }
  const getRoom = () => {
    return (
      WindowWidthLessThan(850)?
        <Room1Mobile onMonitorClick={()=>{
          setOpenMonitor(true)
        }} />
      :
        <Room1 onMonitorClick={()=>{
          setOpenMonitor(true)
        }} />
    )
  }

  const [openMonitor, setOpenMonitor] = useState(null)
  const appVideo = useRef(null)
  if (openMonitor){
    appVideo.current.play()
  } else if (openMonitor===false) {
    appVideo.current.pause()
  }

  return (
    <div className="App">

      <div className="page page1">
        <div id="note">*still under development</div>
        <div className="sky background-style" style={{backgroundImage:`url(${Sky})`}} />
        <div className="ground background-style" style={{backgroundImage:`url(${Ground})`}} />
        <div className="mntn1 background-style" style={{backgroundImage:`url(${Mountain1})`}} />
        <div className="mntn2 background-style" style={{backgroundImage:`url(${Mountain2})`}} />
        <div className="mntn3 background-style" style={{backgroundImage:`url(${Mountain3})`}} />
        <div id="moon" className="background-style" style={{backgroundImage:`url(${Moon})`}} />
        <Hut />
        <div id="main-title">
          <div id="title">Nadim Shahoud</div>
          <div id="subtitle">Software Developer</div>
        </div>
      </div>

      <div className="blank-space" />

      <div className="page page2">
        <div className="page-container">
          {getRoom()}
          <div className="section">
            <div className="section-title">City Guide App</div>
            <div className="section-subtitle">NodeJS, ReactJS, React Native &amp; MySQL</div>
            <div className="section-body">
              <p>
                This app helps users discover businesses around them, and gives businesses the chance to be seen in their area.
              </p>
              <p>
                It shows local shops, restaurants, clinics and so on.
              </p>
              <p>
                Users can order stuff or make a reservation from any business on the app.
              </p>
              <p>
                Business owners, on the other hand, can manage their business through an easy to use dashboard.
                Using the dashboard, the owner can track and manage all their orders, reservations or appointments.
              </p>
            </div>
          </div>
        </div>

        <div
          id="monitor-container"
          style={{
            opacity:openMonitor?1:0,
            left:openMonitor?0:"-150vw",
            backdropFilter:openMonitor&&"blur(2px)"
          }}
        >
          <div
            id="monitor-background"
            onClick={()=>setOpenMonitor(false)}
            style={{ opacity:openMonitor?.1:0, transitionDelay:openMonitor?"250ms":null }}
          />
          <Monitor
            style={{left:!openMonitor&&"-6%", transitionDelay:!openMonitor&&"150ms"}}
            onClick={()=>window.open("https://github.com/nadim-shahoud/CityGuide", "_blank")}
            closeMonitor={()=>setOpenMonitor(false)}
          />
          <Phone
            style={{left:!openMonitor&&"36%"}}
            appVideo={appVideo}
            onClick={()=>window.open("https://github.com/nadim-shahoud/CityGuide", "_blank")}
            closeMonitor={()=>setOpenMonitor(false)}
          />
        </div>
      </div>

      <div className="blank-space" />

      {/* <div className="page page3">
        <div id="cave1" className="caves background-style" style={{backgroundImage:`url(${Cave1})`}} />
        <div id="cave2" className="caves background-style" style={{backgroundImage:`url(${Cave2})`}} />
        <div id="cave3" className="caves background-style" style={{backgroundImage:`url(${Cave3})`}} />
        <div id="cave4" className="caves background-style" style={{backgroundImage:`url(${Cave4})`}} />
        <div id="cave5" className="caves background-style" style={{backgroundImage:`url(${Cave5})`}} />
      </div> */}
    </div>
  );
}

export default App;
