import {useState} from "react"


export default function Monitor(params) {
  return (
    <svg
      className="monitor"
      style={params.style}
      width="737" height="422" viewBox="0 0 737 422" fill="none"
      xmlns="http://www.w3.org/2000/svg"
      onClick={params.closeMonitor}
    >
      <image style={{cursor:"pointer"}} onClick={params.onClick} x="4.5" y="4.5" width="728" height="413" rx="9.5" href={require("../assets/vscode.png")} />
      <rect x="4.5" y="4.5" width="728" height="413" rx="9.5" stroke="black" strokeWidth="9"/>
    </svg>
  )
}
